body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f09819; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #edde5d,
    #f09819
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input:focus{
    border-color:#ffc107 !important;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 4px rgba(221, 218, 34, 0.6) !important;
    outline: 0 none !important;
}
.divider {
    border: 1px solid #ffc107;
    width: 100%;
}
.text-title {
    color: white;
}

.searchContainer hr {
    border: 1px solid #ffc107;
    width: 100%;
}

.searchContainer input:focus {
    border-color:#ffc107;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 4px rgba(221, 218, 34, 0.6);
    outline: 0 none;
}
  
.containerComponents {
    margin-top: 10%;
}
